<template>
  <div class="teacherViewScore"v-loading="loadingStatus">
    <div>
      <div class="textLeft">
        <h4 class="yearTime">
          {{schoolYear}}
        </h4>
      </div>
      <div class="textLeft">
        <h4 class="">
          {{term == 0 ? "上学期" : "下学期"}}
        </h4>
      </div>
      <div class="selectWrap textLeft">
        <h4>
          请选择试卷名称:
        </h4>
        <el-select v-model="value" placeholder="请选择">
          <el-option
            v-for="item in selectData"
            :key="item.id"
            :label="item.examName"
            :value="item.examId">
          </el-option>
        </el-select>
      </div>
    </div>
    <div class="scoreTable">
      <table id="table" v-for="tableList in selectData" v-if="tableList.examId == value">
        <tr v-for="tableTh in tableList.tableData.tableHeader">
          <th v-for="thData in tableTh" :rowspan="thData.rowNum" :colspan="thData.colNum">{{thData.text}}</th>
        </tr>
        <tr ref="tr" v-for="tableTrBody in tableList.tableData.tableBody">
          <td v-for="(tdValue, tdKey) in tableTrBody" v-show="tdKey != 'studentId' ">
            {{tdValue}}
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'teacherViewScore',
    data() {
      return {
        examName:"",
        examId:"",
        gradeName:"",
        className:"",
        schoolYear:"",
        term:"",
        count:"",
        selectData:"",
        value:"",
        loadingStatus:true
      }
    },
    mounted(){
//        页面渲染请求
      let $this = this;
      var scoreId = this.$route.query;
//      this.$ajax('static/tableScore.json',
      this.$ajax('http://60.205.115.173:8888/yitai-educational/teacher/findSubjectStudentScoreTableByCondition?classId=150&examId=1',
//          scoreId
      )
        .then(function (response) {
//             声明初始化变量
          console.log(response);
          if (response.code == 200){
            $this.schoolYear = response.data.schoolYear;
            $this.term = response.data.term;
            $this.selectData = response.data.tableList;
            $this.value = response.data.tableList[0].examId;
            $this.loadingStatus = false;
          }
//  $this.tableData = response.data
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    methods: {
      callBackPages(){
//          返回上一页
        this.$router.go(-1);
      }
    }
  }
</script>
<style scoped>
  .teacherViewScore{
    padding: 30px;
    min-height: 774px;
  }

  .scoreTable #table {
    width: 100%;
    border: 1px solid #dfe6ec;
    margin-top: 20px;
    text-align: center;
  }
  #table tr,td,th {
    border-bottom: 1px solid #dfe6ec;
    border-right: 1px solid #dfe6ec;
  }
  #table th {
    padding: 1% 0.5%;
    font-size: 14px;
    font-weight: 600;
    text-align: center;
    background-color: #eef1f6;
  }
  #table td {
    box-sizing: border-box;
    height: 30px;
  }
  #table td:nth-child(1) {
    width: 80px;
  }
  #table td:nth-child(2) {
    width: 150px;
  }
  .selectWrap h4 {
    display: inline-block;
    margin-right: 30px;
  }
  .textLeft {
    text-align: left;
    margin: 5px 0;
  }
</style>



